/**
 * SEO component that queries for data with
 *  Gatsby's useStaticQuery React hook
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React from "react"
import PropTypes from "prop-types"
import { Helmet } from "react-helmet"
import { useStaticQuery, graphql } from "gatsby"

import poppinsRegularEot from '../assets/fonts/Poppins/poppins-v12-latin-regular.eot'
import poppinsRegularSvg from '../assets/fonts/Poppins/poppins-v12-latin-regular.svg'
import poppinsRegularTtf from '../assets/fonts/Poppins/poppins-v12-latin-regular.ttf'
import poppinsRegularWoff from '../assets/fonts/Poppins/poppins-v12-latin-regular.woff'
import poppinsRegularWoff2 from '../assets/fonts/Poppins/poppins-v12-latin-regular.woff2'
import poppins700Eot from '../assets/fonts/Poppins/poppins-v12-latin-700.eot'
import poppins700Svg from '../assets/fonts/Poppins/poppins-v12-latin-700.svg'
import poppins700Ttf from '../assets/fonts/Poppins/poppins-v12-latin-700.ttf'
import poppins700Woff from '../assets/fonts/Poppins/poppins-v12-latin-700.woff'
import poppins700Woff2 from '../assets/fonts/Poppins/poppins-v12-latin-700.woff2'


// import poppinsRegularEot from '../../static/fonts/Poppins/poppins-v12-latin-regular.eot'
// import poppinsRegularSvg from '../../static/fonts/Poppins/poppins-v12-latin-regular.svg'
// import poppinsRegularTtf from '../../static/fonts/Poppins/poppins-v12-latin-regular.ttf'
// import poppinsRegularWoff from '../../static/fonts/Poppins/poppins-v12-latin-regular.woff'
// import poppinsRegularWoff2 from '../../static/fonts/Poppins/poppins-v12-latin-regular.woff2'
// import poppins700Eot from '../../static/fonts/Poppins/poppins-v12-latin-700.eot'
// import poppins700Svg from '../../static/fonts/Poppins/poppins-v12-latin-700.svg'
// import poppins700Ttf from '../../static/fonts/Poppins/poppins-v12-latin-700.ttf'
// import poppins700Woff from '../../static/fonts/Poppins/poppins-v12-latin-700.woff'
// import poppins700Woff2 from '../../static/fonts/Poppins/poppins-v12-latin-700.woff2'

function SEO({ description, lang, meta, title }) {
  const { site } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
            description
            author
            url
            image
            keywords
          }
        }
      }
    `
  )

  const metaDescription = description || site.siteMetadata.description

  return (
    <Helmet
      htmlAttributes={{
        lang,
      }}
      title={title}
      titleTemplate={`%s | ${site.siteMetadata.title}`}
      meta={[
        {
          name: `description`,
          content: metaDescription,
        },
        {
          name: `keywords`,
          content: `${site.siteMetadata.keywords}`,
        },
        {
          property: `og:title`,
          content: `${title} - Pathfinder Designs`,
        },
        {
          property: `og:description`,
          content: metaDescription,
        },
        {
          property: `og:type`,
          content: `website`,
        },
        {
          property: `og:image`,
          content: site.siteMetadata.image,
        },
        {
          property: `og:image:alt`,
          content: `Pathfinder Designs`,
        },
        {
          property: `og:url`,
          content: site.siteMetadata.url,
        },
        {
          name: `twitter:card`,
          content: `summary_large_image`,
        },
        {
          name: `twitter:creator`,
          content: site.siteMetadata.author,
        },
        {
          name: `twitter:title`,
          content: `${title} - Pathfinder Designs`,
        },
        {
          name: `twitter:description`,
          content: metaDescription,
        },
      ].concat(meta)}
      
    >

      <link rel="preload" as="font" href={poppinsRegularEot} type="font/eot" crossOrigin="anonymous"/>
      <link rel="preload" as="font" href={poppins700Eot} type="font/eot" crossOrigin="anonymous"/>

      <link rel="preload" as="font" href={poppinsRegularSvg} crossOrigin="anonymous"/>
      <link rel="preload" as="font" href={poppins700Svg} crossOrigin="anonymous"/>

      <link rel="preload" as="font" href={poppinsRegularTtf} type="font/ttf" crossOrigin="anonymous"/>
      <link rel="preload" as="font" href={poppins700Ttf} type="font/ttf" crossOrigin="anonymous"/>

      <link rel="preload" as="font" href={poppinsRegularWoff} type="font/woff" crossOrigin="anonymous"/>
      <link rel="preload" as="font" href={poppins700Woff} type="font/woff" crossOrigin="anonymous"/>

      <link rel="preload" as="font" href={poppinsRegularWoff2} type="font/woff2" crossOrigin="anonymous"/>
      <link rel="preload" as="font" href={poppins700Woff2} type="font/woff2" crossOrigin="anonymous"/>
    </Helmet>
    
  )
}

SEO.defaultProps = {
  lang: `pt`,
  meta: [
    {
      name: `copyright`,
      content: `Copyright  2020 Pathfinder Designs. All rights reserved.`
    }
  ],
  description: ``,
}

SEO.propTypes = {
  keywords: PropTypes.string,
  image: PropTypes.string,
  description: PropTypes.string,
  lang: PropTypes.string,
  meta: PropTypes.arrayOf(PropTypes.object),
  title: PropTypes.string.isRequired,
}

export default SEO
