import styled from 'styled-components'
import {motion} from 'framer-motion'
import {theme} from '../globalVariables'


export const FooterContainer = styled(motion.div)`
    padding: 70px 10vw;
    padding-bottom: 40px;
    row-gap: 50px;
    display: flex;
    flex-direction: column;
    align-items: top;
    justify-content: space-between;

    @media screen and (min-width:700px) {
        flex-direction: row;
    }
`
export const FooterWrapper = styled(motion.div)`
    background: black;
    color: white;
`

export const FooterSection = styled(motion.div)`
    * {
        list-style: none;
        text-decoration: none;
        color: white;
    }

    ul {
        line-height: 40px;

        li {

            a {
                &:hover {
                    
                    filter: brightness(0) saturate(100%) ${theme.filterRed};
                }
            }
            
        }
    }
`

export const FooterBrand = styled(motion.div)`

    img {
        height: auto;
        width: 50px;

        &:hover {
            filter: brightness(0) saturate(100%) ${theme.filterRed};
        }
    }
    
`

export const SocialMedia = styled(motion.div)`
    display: flex;
    column-gap: 30px;
    row-gap: 30px;

    img {
        height: auto;
        width: 20px;
        filter: ${theme.invert};

        &:hover {
            filter: ${theme.filterRed};
        }
    }
    
`

export const FooterCopyright = styled(motion.div)`
    width: 100%;
    
    padding: 0 10vw 30px;

    @media screen and (min-width:700px) {
        text-align: center;
    }
`