import styled from 'styled-components';
import {motion} from 'framer-motion'
import {theme} from '../globalVariables'

export const HeaderNav = styled(motion.div)`
    height: 0px;
    width: 100%;
    position: absolute;
    top: 72px;
    right: 0;
    left: 0%;
    z-index: 99;
`
export const Logo = styled.div`

    img {
        height: auto;
        width: 40px;
        filter: ${theme.filterToDefaultBlack};

        &:hover {
            filter: brightness(0) saturate(100%) ${theme.filterRed};
        }
    }

    a {
        font-size: 1.8rem;
        font-weight: 700;
        color: ${theme.text}
    }


    span {
        cursor: pointer;
        height: 1rem;
        width: 1rem;
        background: ${theme.red};
        margin: 0 5px;
        border-radius: 100%;
        display: inline-block;
        position: relative;
        bottom: 0px;
    }
`
