import React from 'react'

import { HeaderNav, Logo } from '../styles/headerStyles'
import { Container, Flex, Button } from '../styles/globalStyles'

import {transitionColor} from '../globalVariables'
import AniLink from "gatsby-plugin-transition-link/AniLink"

import logo from '../assets/images/logo/logo.svg'


const Header = () => {

    // // const dispatch = useGlobalDispatchContext()
    // const currentTheme = useGlobalStateContext()

    // const toggleTheme = () => {
    //     if(currentTheme === 'dark') {
    //         dispatch({
    //             type: 'TOGGLE_THEME',
    //             theme: 'light',
    //         }) 
    //     } else {
    //         dispatch ({
    //             type: 'TOGGLE_THEME',
    //             theme: 'dark',
    //         }) 
    //     }
    // }

    // Save Changes Localy
    // useEffect(() => {
    //     window.localStorage.setItem('theme', currentTheme)
    // }, [currentTheme])

    return (
        <HeaderNav
            animate={{y:0, opacity: 1}}
            initial={{y:-72, opacity: 0}}
            transition={{duration: 1, ease: [.6, .05, -.01, 0.9]}}
        >
            <Container>
                <Flex spaceBetween noHeight>
                    <Logo>
                        <AniLink 
                            cover
                            direction='right'                                            
                            bg={transitionColor}
                            to='/'><img src={logo} alt="phtfndr" /></AniLink>                        
                    </Logo>
                    
                    <Button>
                        
                        <AniLink 
                            cover
                            direction='right'                                            
                            bg={transitionColor}
                            to='/contact'><button>Contact us</button></AniLink>
                    </Button>
                </Flex>
                
            </Container>
        </HeaderNav>
    )
}

export default Header